<template>
  <div>
    <template>
      <material-card
        color="accent"
        full-header
      >
        <template #heading>
          <v-tabs
            v-model="tab"
            background-color="transparent"
            slider-color="transparent"
            class="pa-8"
          >
            <v-tab
              href="#menu-1"
              @click="formType = 'create'"
            >
              DATA USER
            </v-tab>
            <v-tab
              href="#menu-2"
              @click="formType = 'create' , clear()"
            >
              {{ formType === 'create' ? 'Add User' : 'Edit User' }}
            </v-tab>
          </v-tabs>
        </template>
        <v-tabs-items
          v-model="tab"
          background-color="transparent"
        >
          <v-tab-item
            v-for="n in 2"
            :key="n"
          >
            <v-card-text>
              <template>
                <v-row
                  align="center"
                  class="flex-nowrap"
                >
                  <v-col cols="1">
                    <v-list-item-action>
                      <v-simple-checkbox color="secondary" />
                    </v-list-item-action>
                  </v-col>

                  <v-col
                    class="font-weight-light"
                    cols="8"
                  />
                  <v-col
                    cols="auto"
                    class="text-right"
                  >
                    <v-icon class="mx-1">
                      mdi-pencil
                    </v-icon>

                    <v-icon
                      class="mx-1"
                      color="error"
                    >
                      mdi-close
                    </v-icon>
                  </v-col>
                </v-row>
              </template>
            </v-card-text>
          </v-tab-item>
        </v-tabs-items>
      </material-card>
    </template>

    <v-tabs-items v-model="tab">
      <v-tab-item id="menu-1">
        <v-card flat>
          <v-card-text>
            <v-data-table
              :headers="headers"
              :items="users"
              :search="search"
              sort-by="calories"
              class="elevation-1"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-text-field
                    v-model="search"
                    class="mx-4"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                    align-right
                  />
                  <v-spacer />
                </v-toolbar>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-btn
                  icon
                  small
                  @click="edit(item.id)"
                >
                  <v-icon>mdi-pencil</v-icon>
                </v-btn>
                <v-btn
                  icon
                  small
                  @click="deleteUser(item.id)"
                >
                  <v-icon>mdi-delete</v-icon>
                </v-btn>
              </template>
              <template v-slot:item.online="{ item }">
                <v-chip
                  v-if="item.online == 1"
                  class="ma-2"
                  color="green"
                  text-color="white"
                  x-small
                  @click="changeStatus(item.id)"
                >
                  Online
                </v-chip>
                <v-chip
                  v-else
                  class="ma-2"
                  color="red"
                  text-color="white"
                  x-small
                  @click="changeStatus(item.id)"
                >
                  Offline
                </v-chip>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item id="menu-2">
        <v-card
          flat
          class="pa-5"
        >
          <v-form>
            <v-row>
              <v-col class="px-9">
                <v-text-field
                  v-model="name"
                  :error-messages="nameErrors"
                  label="Name"
                  required
                  @input="$v.name.$touch()"
                  @blur="$v.name.$touch()"
                />
                <v-text-field
                  v-model="email"
                  :error-messages="emailErrors"
                  label="E-mail"
                  required
                  @input="$v.email.$touch()"
                  @blur="$v.email.$touch()"
                />
                <v-text-field
                  v-model="phone"
                  :error-messages="phoneErrors"
                  label="Phone"
                  required
                  @input="$v.phone.$touch()"
                  @blur="$v.phone.$touch()"
                />
                <v-text-field
                  v-model="phone2"
                  :error-messages="phoneErrors"
                  label="Phone 2"
                  required
                  @input="$v.phone.$touch()"
                  @blur="$v.phone.$touch()"
                />
                <v-text-field
                  v-if="formType === 'create'"
                  v-model="password"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show1 ? 'text' : 'password'"
                  :error-messages="passwordErrors"
                  label="Password"
                  required
                  @input="$v.password.$touch()"
                  @blur="$v.password.$touch()"
                  @click:append="show1 = !show1"
                />

                <v-text-field
                  v-if="formType === 'create'"
                  v-model="passwordConfirmation"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show1 ? 'text' : 'password'"
                  :error-messages="passwordConfirmationErrors"
                  label="Password Confirmation"
                  required
                  @input="$v.passwordConfirmation.$touch()"
                  @blur="$v.passwordConfirmation.$touch()"
                  @click:append="show1 = !show1"
                />
                <v-select
                  v-model="city_id"
                  :items="city"
                  item-text="nama"
                  item-value="city_id"
                  label="City"
                  required
                  @change="filterDistrict"
                />
                <v-select
                  v-model="district_id"
                  :items="filteredDistrict"
                  item-text="nama"
                  item-value="id"
                  label="District"
                  :disabled="city_id === null"
                  required
                  @change="filterVillage"
                />
                <v-select
                  v-if="filteredVillage.length > 0"
                  v-model="village_id"
                  :items="filteredVillage"
                  item-text="nama"
                  item-value="id"
                  label="Village"
                  required
                />

                <v-select
                  v-else
                  v-model="village_id"
                  disabled
                  :items="filteredVillage"
                  item-text="nama"
                  item-value="id"
                  label="Select a Village"
                />
              </v-col>
              <v-col class="px-9 pt-8">
                <v-select
                  v-model="role_id"
                  :items="role"
                  item-text="name"
                  item-value="id"
                  :error-messages="roleErrors"
                  label="Role"
                  required
                  @change="$v.role.$touch()"
                  @blur="$v.role.$touch()"
                />
                <v-textarea
                  v-model="address"
                  label="Address"
                  value=""
                  hint="Input Address"
                />
                <v-file-input
                  v-model="photo"
                  :rules="rules"
                  accept="image/jpeg"
                  prepend-icon="mdi-camera"
                  label="Photo"
                  @change="preview($event)"
                />
                <div
                  v-if="imgUrl"
                >
                  <v-img
                    class="preview"
                    :src="imgUrl"
                  />
                </div>
                <div>
                  <v-img
                    class="preview"
                    :src="$baseUrl+photo"
                  />
                </div>
              </v-col>
            </v-row>

            <v-btn
              v-if="formType === 'create'"
              class="mr-4 mb-5 float-right"
              @click="save"
            >
              Save
            </v-btn>
            <v-btn
              v-else
              class="mr-4 mb-5 float-right"
              @click="update"
            >
              Update
            </v-btn>
            <!-- <v-btn
              class="mr-4 float-right"
              @click="clear"
            >
              clear
            </v-btn> -->
          </v-form>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
  import axios from 'axios'
  import { validationMixin } from 'vuelidate'
  import {
    required,
    maxLength,
    email,
    minLength,
    sameAs,
  } from 'vuelidate/lib/validators'
  export default {
    mixins: [validationMixin],

    validations: {
      name: { required, maxLength: maxLength(20) },
      email: { required, email },
      password: { required, minLength: minLength(8) },
      role: { required },
      passwordConfirmation: {
        required,
        sameAsPassword: sameAs('password'),
        minLength: minLength(8),
      },
      phone: { required, minLength: minLength(10) },
    },
    data: () => ({
      rules: [
        value =>
          !value ||
          value.size < 2000000 ||
          'Avatar size should be less than 2 MB!',
      ],
      search: '',
      filteredVillage: [],
      filteredDistrict: [],
      show1: false,
      tab: null,
      dialog: false,
      dialogDelete: false,
      password: '',
      passwordConfirmation: '',
      district_id: null,
      village_id: null,
      city: [],
      city_id: null,
      user_id: 0,
      name: '',
      email: '',
      phone: '',
      phone2: '',
      address: '',
      role_id: null,
      district: [],
      village: [],
      imgUrl: null,
      photo: null,
      role: [],
      formType: 'create',
      headers: [
        {
          text: 'Name',
          align: 'start',
          sortable: true,
          value: 'name',
        },
        { text: 'Email', value: 'email' },
        { text: 'No. Telp', value: 'phone' },
        { text: 'Address', value: 'address' },
        { text: 'Role', value: 'role' },
        { text: 'Status', value: 'online' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      users: [],
      filterUsers: [],
    }),
    computed: {
      checkboxErrors () {
        const errors = []
        if (!this.$v.checkbox.$dirty) return errors
        !this.$v.checkbox.checked && errors.push('You must agree to continue!')
        return errors
      },
      roleErrors () {
        const errors = []
        if (!this.$v.role.$dirty) return errors
        !this.$v.role.required && errors.push('Item is required')
        return errors
      },
      nameErrors () {
        const errors = []
        if (!this.$v.name.$dirty) return errors
        !this.$v.name.maxLength &&
          errors.push('Name must be at most 10 characters long')
        !this.$v.name.required && errors.push('Name is required.')
        return errors
      },
      emailErrors () {
        const errors = []
        if (!this.$v.email.$dirty) return errors
        !this.$v.email.email && errors.push('Must be valid e-mail')
        !this.$v.email.required && errors.push('E-mail is required')
        return errors
      },
      passwordErrors () {
        const errors = []
        if (!this.$v.password.$dirty) return errors
        !this.$v.password.minLength && errors.push('At Least 8 Characters')
        !this.$v.password.required && errors.push('Password is required')
        return errors
      },

      passwordConfirmationErrors () {
        const errors = []
        if (!this.$v.passwordConfirmation.$dirty) return errors
        !this.$v.passwordConfirmation.sameAsPassword &&
          errors.push('Passwords must be identical')
        !this.$v.passwordConfirmation.required &&
          errors.push('Password Confirmation is required')
        return errors
      },
      phoneErrors () {
        const errors = []
        if (!this.$v.phone.$dirty) return errors
        !this.$v.phone.minLength &&
          errors.push('Phone must be at most 12 characters long')
        !this.$v.phone.required && errors.push('phone is required.')
        return errors
      },
    },
    created () {
      this.getUsers()
      this.getRole()
      this.getArea()
    },

    methods: {
      getUsers () {
        axios
          .get('admin/user')
          .then(res => {
            this.users = res.data.data
          })
          .catch(error => {
            console.log(error)
          })
      },
      getRole () {
        axios.get('admin/role').then(res => {
          this.role = res.data.data
        })
      },

      getArea () {
        axios.get('admin/user/area').then(res => {
          this.district = res.data.district
          this.village = res.data.village
          this.city = res.data.special_city
        })
      },
      save () {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('name', this.name)
        form.append('email', this.email)
        form.append('password', this.password)
        form.append('password_confirmation', this.passwordConfirmation)
        form.append('role_id', this.role_id)
        form.append('address', this.address)
        form.append('phone', this.phone)
        form.append('phone2', this.phone)
        form.append('photo', this.photo)
        form.append('city_id', this.city_id)
        form.append('district_id', this.district_id)
        form.append('village_id', this.village_id)
        axios
          .post('auth/register', form)
          .then(() => {
            this.$swal(
              'Data Created Successfully',
              '',
              'success',
            )
            this.$v.$reset()
            this.name = ''
            this.email = ''
            this.password = ''
            this.passwordConfirmation = ''
            this.select = null
            this.city_id = null
            this.photo = null
            this.phone = null
            this.phone2 = null
            this.address = null
            this.role_id = null

            this.getUsers()
          })
      },

      submit () {
        this.$v.$touch()
      },
      clear () {
        this.$v.$reset()
        this.user_id = 0
        this.name = ''
        this.email = ''
        this.password = ''
        this.passwordConfirmation = ''
        this.photo = null
        this.phone = null
        this.address = null
        this.role_id = null
        this.imgUrl = null
        this.city_id = null
        this.district_id = null
        this.village_id = null
      },

      preview (e) {
        if (this.photo !== null) {
          this.photo = e
          this.imgUrl = URL.createObjectURL(this.photo)
        } else {
          this.imgUrl = null
        }
      },

      filterVillage () {
        this.filteredVillage = []
        this.filteredVillage = this.village.filter(
          village => village.kecamatan_id === this.district_id,
        )
      },

      filterDistrict () {
        this.filteredDistrict = []
        this.filteredDistrict = this.district.filter(
          district => district.kabupaten_id === this.city_id,
        )
      },

      edit (id) {
        this.formType = 'edit'
        this.filterUsers = []
        this.filterUsers = this.users.filter(
          users => users.id === id,
        )
        this.user_id = parseInt(this.filterUsers[0].id)
        this.name = this.filterUsers[0].name
        this.email = this.filterUsers[0].email
        this.password = this.filterUsers[0].password
        this.passwordConfirmation = this.filterUsers[0].password
        this.photo = this.filterUsers[0].photo
        this.phone = this.filterUsers[0].phone
        this.phone2 = this.filterUsers[0].phone2
        this.address = this.filterUsers[0].address
        this.role_id = parseInt(this.filterUsers[0].role_id)
        this.city_id = this.filterUsers[0].city_id
        this.district_id = this.filterUsers[0].district_id
        this.filteredDistrict = this.district.filter(
          district => district.kabupaten_id === this.city_id,
        )
        this.filteredVillage = this.village.filter(
          village => village.kecamatan_id === this.district_id,
        )
        this.village_id = this.filterUsers[0].village_id
        this.tab = 'menu-2'
      },

      update () {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('id', this.user_id)
        form.append('name', this.name)
        form.append('email', this.email)
        form.append('role_id', this.role_id)
        form.append('address', this.address)
        form.append('phone', this.phone)
        form.append('photo', this.photo)
        form.append('city_id', this.city_id)
        form.append('district_id', this.district_id)
        form.append('village_id', this.village_id)
        axios
          .post('auth/update', form)
          .then(() => {
            this.$swal(
              'Data Created Successfully',
              '',
              'success',
            )
            this.$v.$reset()
            this.getUsers()
            this.tab = 'menu-1'
          })
      },

      changeStatus (id) {
        axios.get('auth/change-status/' + id).then(() => {
          this.$swal('Data Updated Successfully', '', 'success')
          this.getUsers()
        })
      },

      deleteUser (id) {
        axios.get('admin/user/delete/' + id).then(() => {
          this.$swal('Data Deleted Successfully', '', 'success')
          this.getUsers()
        })
      },
    },
  }
</script>

<style>
.preview {
  width: 200px;
  height: 200px;
  justify-content:right
}
</style>
